@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Regular.ttf");
  font-weight: 400;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Bold.ttf");
  font-weight: 700;
}

@font-face {
  font-family: "fontawesome";
  src: url("../fonts/fontawesome/fontawesome-duotone.ttf");
  font-weight: 900;
}

@font-face {
  font-family: "fontawesome";
  src: url("../fonts/fontawesome/fontawesome-light.ttf");
  font-weight: 300;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  font-family: "Montserrat";
  background: #f1f1f1;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

::before,
::after {
  font-family: "fontawesome";
}

.icon__search::before {
  content: '\f002';
  color: #000;
  font-weight: 300;
}

.icon__like::before {
  content: '\f004';
  color: #000;
  font-weight: 300;
}

.icon__bag::before {
  content: '\f290';
  color: #000;
  font-weight: 300;
}

.icon__person::before {
  content: '\f406';
  color: #000;
  font-weight: 300;
}

.ads {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 0;
}

.ads__lang {
  text-transform: uppercase;
  font-weight: 400;
  font-size: 18px;
  color: #323232;
}

.ads__icon {
  font-size: 21px;
  line-height: 20px;
}

.logo {
  color: #323232;
  font-weight: 900;
  font-size: 32px;
  text-transform: uppercase;
}

.nav {
  padding-left: 0;
  padding-right: 0;
}

.nav::before, .nav::after {
  content: '';
  background: #c4c4c4;
  height: 1px;
  display: block;
  width: 100%;
}

.nav__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px;
}

@media (max-width: 992px) {
  .nav__menu .menu {
    display: block;
  }
}

.menu {
  display: flex;
  margin: 20px 0;
}

.menu__link {
  margin-right: 30px;
  font-weight: 400;
  color: #323232;
  padding: 10px;
  transition: 0.5s;
}

.menu__link:hover {
  color: #fff;
  background: #000;
  padding: 10px;
  border-radius: 5px;
}

.menu__icon {
  line-height: 19px;
  font-size: 24px;
  margin-left: 30px;
  padding: 10px;
}

.glav__main {
  margin-top: 40px;
  justify-content: space-between;
}

.main {
  position: relative;
  background: #e1e1e1;
  max-width: 740px;
  width: 100%;
  padding: 20px 27px 181px 13px;
}

.main__info {
  margin-bottom: 62px;
  display: flex;
  align-items: start;
  justify-content: space-between;
}

.main__icon {
  line-height: 18px;
  font-size: 23px;
  background: #fff;
  border-radius: 8px;
  padding: 16px;
  display: block;
  margin-bottom: 15px;
}

.main__content {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.main__content-info {
  margin-left: 40px;
}

.main__content-title {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 16px;
}

.main__content-desc {
  font-size: 18px;
}

.main__other {
  position: relative;
  background: #e1e1e1;
  max-width: 370px;
  width: 100%;
  padding: 20px;
  text-align: center;
  height: 420px;
}

.main__other:first-child {
  margin-bottom: 30px;
}

.main__other-info {
  display: flex;
  align-items: start;
  justify-content: space-between;
  margin-bottom: 32px;
}

.main__other-img {
  margin: 0 30px 0 20px;
}

.discount__info {
  background: #323232;
  color: #fff;
  padding: 10px 30px;
  border-radius: 8px;
  font-size: 20px;
  display: inline-block;
}
